body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* CSS HEX */
:root {
    --charcoal: #264653ff;
    --persian-green: #2a9d8fff;
    --orange-yellow-crayola: #e9c46aff;
    --sandy-brown: #f4a261ff;
    --burnt-sienna: #e76f51ff;
    --off-white: #f0efeb;
}

body {
    background-color: #264653ff;
    background-color: var(--charcoal);
    color: #f0efeb;
    color: var(--off-white);
}

.App {
  /* text-left: center; */
  padding-left: 10%;
}
.GHlogo {
    border-style: solid;
    border-radius: 10%;

    width: 10%;
    height: 10% auto;
    max-width: 120px;
}
.GHLogoLink {
    color: #e76f51ff;
    color: var(--burnt-sienna);
}
.GHLogoLink:hover {
    color: #e9c46aff;
    color: var(--orange-yellow-crayola);
}
.ItemList{
    list-style-type: none;
}
.ListGHmark {
    display: inline-block;
    vertical-align: middle;
    margin-left: 40%;
    width: 4vw;
    max-width: 30px;
    height: auto;
}
/* .ListDiv {
} */
.ListLink {
    display: inline-block;
    vertical-align: middle;
    text-decoration: none;
    font-size: 1.5em;
    color: #e76f51ff;
    color: var(--burnt-sienna);
}
.ListLink:hover {
    color: #e9c46aff;
    color: var(--orange-yellow-crayola);
}
.Header1{
    font-size: 3em;
}
.Header2{
    font-size: 2em;
}
canvas {
    width: 100vw;
    height: 100vh;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -9999;
}
